<script>
  import { count } from "../store/store.js";

  /**
   * Increases the count variable from the store by 1
   */
  function incrementCount() {
    count.set($count + 1);
  }
</script>

<style type="text/postcss">.button{--bg-opacity:1;background-color:#2b6cb0;background-color:rgba(43,108,176,var(--bg-opacity));--text-opacity:1;color:#fff;color:rgba(255,255,255,var(--text-opacity));font-weight:700;padding:.5rem 1rem;border-radius:.25rem}
/*# sourceMappingURL=src/components/HelloWorld.svelte.map */</style>

<div class="flex items-center justify-center h-screen bg-gray-200">
  <button class="button" on:click={() => incrementCount()}>
    Count is {$count}
  </button>
</div>
